import {useEffect,useState,useRef} from "react";
import {connect} from 'react-redux'
import classes from './send.module.css'
import {SET_CONVERSATION} from "../../store/actions/actions";
import postAsync from "../../store/actions/postAsync";
import getAsync from "../../store/actions/getAsync";
function Send({getConversation,sendConversation,conversation , user , socket}) {
    const myID = user._id
    const holder = useRef(null)

    const [message, setMessage]= useState('')
    const [myMSG, setMyMSG]= useState([])
    useEffect(()=>{
        if(socket){
        socket.on('recieve_message_from_admin' , (m)=>{
            if(!m.fromMe){
                document.querySelector('#audio').play()
            }
            console.log('3ady')
            setMyMSG((ss)=>[...ss ,m])
        })
    }
    },[socket])

    useEffect( ()=>{
        async function fetchData(){
          let tt = await  getConversation()
          setMyMSG(tt.messages)
        }
        fetchData()
    },[getConversation]) 

    useEffect(()=>{
        holder.current.scroll(0, holder.current.scrollHeight)  
    })
    let send =(e)=>{
        // holder.current.innerText = e.keyCode
        if(e.keyCode ===13 || e.type ==='click'){

            const newMessage ={
                from:myID,
                avatar:user.avatar,
                content:message,
                name :user.name,
                date:Date.now()
            }
            socket.emit('send_message_to_admins',newMessage)
            setMessage('')
            sendConversation({theMessage:message})
        }
    }
    let saveInput =(e)=>{
        setMessage(e.target.value)
    }     

    return(
        <div className={classes.container}>
            <div  ref={holder} className={classes.holder}>
                {
                   
                myMSG[0]?    
                myMSG.map((conv)=>{
                   
                    return(<div className={classes.box} style={conv.from ===myID? {marginRight:'auto',marginLeft:0}:{}}>
                        <p style={{paddingBottom:'5px' , borderBottom:'1px solid black'}}>{conv.name}</p>
                        <p>{conv.content}</p>
                        <p className={classes.date}>{new Date(conv.date).toLocaleString()}</p>
                    </div>)
            }) 
                    :<h1 style={{textAlign:'center'}}>loading..</h1>
                }
            </div>         
            {/* <textarea onKeyUp={send} value={message} style={{width:'50%',margin:'0 auto',borderRadius:'15px',resize:'none'}} onChange={saveInput}> */}
            <input className={classes.input} type='text' onKeyUp={send} value={message} onChange={saveInput}/>

            {/* </textarea> */}
            <button className={classes.button} onClick={send} >Send</button>
        </div>
    )
}
const mapStateToprop =(state)=>({
    conversation :state.conversation,
    user : state.user,
    socket:state.socket

})
const mapDispatchToprop =(dispatch)=>({
    sendConversation :(body)=>dispatch(postAsync('/conversation/send',body,SET_CONVERSATION)),
    getConversation:(body)=>dispatch(getAsync('/conversation',SET_CONVERSATION))
})


export default connect(mapStateToprop,mapDispatchToprop)(Send)